.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.weatherContainer {   
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 800px){
  .weatherContainer { 
    display: inline-block;
  }
}
.tempAndDescContainer {
  width: 400px;
  justify-content: center;
  align-items: center;
  color: rgb(207, 224, 239);
  margin-top: -30px;
  margin-bottom: 20px;
}
#submitForm {
  margin-top: 35px;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
}
body {
  background-color: #282c34;
}
h1 {
  color: rgb(207, 224, 239);
}
label {
  color: rgb(207, 224, 239);
  margin-right: 10px;
}
.imageContainer {
  width: 400px;
  height: 300px;
}
.imageContainer img {
  border: 2px solid rgb(72, 85, 96);
}
p {
  color: rgb(131, 146, 159);
}
button {
  background-color: #1c1e23;
  color: rgb(184, 205, 222);
  border: 1px solid rgb(139, 158, 173);
  margin-left: 5px;
  border-radius: 10%;
}
input {
  background-color: #464b57;
  color: rgb(184, 205, 222);
  border: 1px solid rgb(105, 148, 180);
}
.weatherContainerHide {
  visibility: hidden;
}